@font-face {
  font-family: "im-line-icons";
  src: url("./../assets/im-line-icons.eot?c62ink");
  src: url("./../assets/im-line-icons.eot?c62ink#iefix")
      format("embedded-opentype"),
    url("./../assets/im-line-icons.ttf?c62ink") format("truetype"),
    url("./../assets/im-line-icons.woff?c62ink") format("woff"),
    url("./../assets/im-line-icons.svg?c62ink#im-line-icons") format("svg");
  font-weight: normal;
  font-style: normal;
}

[class^="im-icon-"],
[class*=" im-icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "im-line-icons" !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.im-icon-toolbar {
  font-size: 16px;
  align-self: center;
  justify-content: center;
  text-align: center;
}
.im-icon-12 {
  font-size: 12px;
  align-self: center;
  justify-content: center;
  text-align: center;
  color: #666;
}

.im-icon-16 {
  font-size: 16px;
  align-self: center;
  justify-content: center;
  text-align: center;
  color: $icon;
}

.im-icon-20 {
  font-size: 20px;
  align-self: center;
  text-align: center;
  justify-content: center;
  color: $icon;
}

.im-icon-sm,
.im-icon-sm-disabled {
  padding-top: 0;
  text-align: center;
  display: flex;
  border-radius: 2px;
  justify-content: center;
  transition: 0.3s background-color;
}

.im-icon-sm:hover {
  background-color: $icon-hover;
  transition: 0.3s background-color;
}

.im-icon-key {
  height: 16px;
  width: 16px;
  margin-right: 2px;
  margin-left: 2px;
  vertical-align: text-bottom;
  padding-bottom: 1px;
}

